.footer {
  height: 10vh;
  width: 100vw;
  background-color: rgb(15, 15, 15);
  color: white;

  border-top: 5px solid rgb(71, 71, 71);

  display: flex;
  justify-content: center;
  align-items: center;
}

  .footer__copyright {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(71, 71, 71);
  }

  @media (max-height: 600px) {
    .footer__copyright {
      flex-direction: row;
    }
      .footer__copyright span:first-child {
        margin-right: 10px;
      }
  }

  @media (max-height: 400px) {
    .footer {
      font-size: 15px;
    }
  }

  @media (max-height: 300px) {
    .footer {
      font-size: 12px;
    }
  }
